"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"

const MATRIX_CHARS = "10"

export default function Home() {
  const [file, setFile] = useState<File | null>(null)
  const [domain, setDomain] = useState("")
  const [results, setResults] = useState<string[]>([])

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setFile(e.target.files[0])
    }
  }

  const handleExtract = () => {
    // Simulated extraction results
    setResults([
      "T507O}=[NJ",
      ":^|PQ<377|^Q9WW3",
      "1&%^P[?,V#6$_4&:BRXL.B35Y",
      "S.S5EVE>CI5V",
      "MIR%:.U3*$8750",
      "L:MBWLJKWUJO5KA:ES",
      "2>UN6GC%SZ&"
    ])
  }

  const matrixBackground = Array.from({ length: 48 }).map((_, i) => (
    <div key={i} className="text-green-500 animate-pulse">
      {Array.from({ length: 20 }).map((_, j) => (
        <div key={j} className="text-center">
          {MATRIX_CHARS[Math.floor((i + j) % 2)]}
        </div>
      ))}
    </div>
  ))

  return (
    <main className="min-h-screen bg-black text-green-500 p-8 relative overflow-hidden">
      {/* Matrix-like background */}
      <div className="absolute inset-0 opacity-10">
        <div className="grid grid-cols-12 gap-4 h-full">
          {matrixBackground}
        </div>
      </div>

      <div className="relative z-10">
        <h1 className="text-4xl font-mono mb-8 text-center font-bold">
          HellCorporation Extract 🔓
        </h1>

        <Card className="max-w-2xl mx-auto bg-black/50 border border-green-500/30 p-6 backdrop-blur-sm">
          <div className="space-y-6">
            <div>
              <h2 className="text-xl font-mono mb-2">{`>>`} Herramienta de análisis para extraer logins de archivos url:login:pass</h2>
              <p className="text-green-400/70 font-mono">{`>>`} Filtra por dominio como &apos;netflix.com&apos;</p>
            </div>

            <div className="space-y-4">
              <div>
                <p className="font-mono mb-2">{`>>`} Archivo cargado: {file?.name || "Ninguno"}</p>
                <Button 
                  variant="outline" 
                  className="w-full border-green-500 text-green-500 hover:bg-green-500/20"
                  onClick={() => document.getElementById("file-upload")?.click()}
                >
                  [+] Cargar archivo
                </Button>
                <input
                  id="file-upload"
                  type="file"
                  className="hidden"
                  onChange={handleFileUpload}
                />
              </div>

              <Input
                placeholder="netflix.com"
                value={domain}
                onChange={(e) => setDomain(e.target.value)}
                className="bg-transparent border-green-500 text-green-500 placeholder:text-green-500/50"
              />

              <Button 
                className="w-full bg-green-500 hover:bg-green-600 text-black font-bold"
                onClick={handleExtract}
              >
                Extraer logins
              </Button>
            </div>

            {results.length > 0 && (
              <div className="mt-6 space-y-2 font-mono">
                {results.map((result, index) => (
                  <p key={index} className="text-green-400">{`>>`} {result}</p>
                ))}
              </div>
            )}
          </div>
        </Card>
      </div>
    </main>
  )
}
